<template>
  <div>
    <v-row
      v-if="barbershops.length > 0"
      class="barbearias"
    >
      <v-col
        v-for="(barbershop, index) in barbershops"
        :key="index"
        cols="12"
        sm="6"
        md="4"
        class="py-0"
      >
        <barbershop-item
          class="d-block mt-2 my-sm-4 my-lg-8" 
          :barbershop="barbershop" 
        />
      </v-col>
    </v-row>

    <div 
      v-else
      class="d-flex justify-center align-center barbershop-empty"
    >
      <p>
      Nenhuma barbearia encontrada.
    </p>
    </div>
  </div>
</template>

<script>
// Modules
import './Barbershop.scss'
// Components
import BarbershopItem from '@/components/site/barbershop/item/BarbershopItem'
// Mixins
import BarbershopMixin from '@/mixins/BarbershopMixin'

export default {
  name: 'Barbershop',
	components: { BarbershopItem },
	mixins: [ BarbershopMixin ],
	data: () => ({
		barbershops: []
	}),
	mounted () {
		this.getBarbershopAllOnService()
  },
  methods: {
    onGetBarbershopAllOnServiceSuccess (response) {
      this.barbershops = response
      this.$emit('loaded')
    },
    onGetBarbershopAllOnServiceError (err) {
      window.console.log(err)
      this.barbershops = []
      this.$emit('loaded')
    }
  }
}
</script>